.new__list {
  padding-left: 20px;
}

.new__item {
  margin-bottom: 20px;
  list-style: none;
}

.new__wrapper {
  display: flex;
  text-decoration: none;
  color: black;
}

.new__description {
  margin-top: 20px;
  margin-left: 10px;
}

.new__text-title {
  margin: 0;
  padding-bottom: 5px;
  font-size: 11px;
  text-transform: uppercase;
}

.new__text {
  margin: 0;
  font-style: italic;
}

@media (min-width: $desktop-width) {
  .new {
    display: none;
  }
}
