.page-header {
  position: relative;
  width: 100%;
  min-height: 53px;
  border-bottom: 1px solid #C0C0C0;
}

.page-header__logo img {
  position: absolute;
  top: 7px;
  left: 44%;
  width: 44px;
  height: 35px;
  z-index: 1;
}

.page-header__search {
  position: absolute;
  top: 3px;
  left: 12%;
  z-index: 1;
}

.page-header__favorites img {
  position: absolute;
  top: 6px;
  left: 79%;
  z-index: 1;
}

.page-header__basket img {
  position: absolute;
  top: 6px;
  left: 89%;
  z-index: 1;
}

.page-header__ticker {
  display: none;
}

.page-header__btn-menu {
  padding: 5px;
  position: absolute;
  top: 8px;
  left: 12px;
  cursor: pointer;
  transition: right .23s;
  z-index: 3;
  width: 20px;
  transform: translateZ(0);
  backface-visibility: hidden;
}

.page-header__btn-menu span {
  display: block;
  height: 0.7pt;
  background-color: #444;
  margin: 5px 0 0;
  transition: all .1s linear .23s;
  position: relative;
}

.page-header__ticker:checked ~ .page-header__btn-menu {
  left: 325px;
  top: 2px;
  width: 16px;
}

.page-header__ticker:checked ~ .main-nav {
  left: 0;
}

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__first {
  transform: rotate(45deg);
}

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__second {
  display: none;
}

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__third {
  transform: rotate(-45deg);
  top: -6px;
}

.page-header__search-button {
  display: none;
}

.page-header__user {
  display: none;
}

@media (min-width: $desktop-width) {
  .page-header {
    border-bottom: none;
  }

  .page-header__wrapper {
    position: relative;
    max-width: 93.5rem;
    margin: 0 auto;
  }

  .page-header__btn-menu {
    display: none;
  }

  .page-header__logo img {
    top: 50px;
    left: 48%;
    width: 64px;
    height: 48px;
  }

  .page-header__search {
    top: 4px;
    left: 87%;
  }

  .page-header__search-button {
    display: block;
    width: 160px;
    height: 28px;
    border: 2px solid white;
    background-color: #f2f2f2;
    border-radius: 25px;
  }

  .page-header__search img {
    position: absolute;
    top: -8px;
    left: 115px;
  }

  .page-header__favorites img {
    top: 90px;
    left: 93%;
  }

  .page-header__basket img {
    top: 90px;
    left: 96%;
  }

  .page-header__user {
    display: block;
  }

  .page-header__user img {
    position: absolute;
    top: 90px;
    left: 90%;
    z-index: 1;
  }
}
