.main-nav {
  display: block;
  position: fixed;
  list-style:none;
  padding-top: 23px;
  margin: 0;
  box-sizing: border-box;
  width: 350px;
  background-color: white;
  box-shadow: -8px 20px 16px 10px #444;
  height: 100%;
  top: 0;
  left: -400px;
  transition: left .3s;
  z-index: 2;
  transform: translateZ(0);
  backface-visibility: hidden;
  overflow: scroll;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  margin-top: 8px;
  padding-bottom: 5px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  border-bottom: 1px solid #C0C0C0;
}

.main-nav__item {
  list-style: none;
}

.main-nav__item a {
  text-transform: uppercase;
  text-decoration: none;
  color: #70707070;
  font-size: 14px;
}

.main-nav__item--active a {
  color: black;
  padding-bottom: 5px;
  border-bottom: 3px solid black;
}

@media (min-width: $desktop-width) {
  .main-nav {
    position: static;
    padding-top: 100px;
    width: 100%;
    background-color: rgba(255,255,255,.5);
    box-shadow: none;
    overflow: hidden;
  }

  .main-nav__list {
    justify-content: flex-start;
    padding-left: 60px;
    padding-bottom: 12px;
    max-width: 90rem;
    margin: 0 auto;
  }

  .main-nav__item {
    padding-right: 30px;
  }

  .main-nav__item--active a {
    padding-bottom: 12px;
  }

  .main-nav__item a:hover {
    padding-bottom: 12px;
    border-bottom: 3px solid black;
    color: black;
  }
}
