/* stylelint-enable */
html,
body {
  min-width: 320px; }

body {
  padding: 0;
  margin: 0 auto;
  width: auto;
  font-family: "Quicksand", "Arial", sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: black; }

@media (min-width: 1024px) {
  body {
    width: auto; } }

.page-header {
  position: relative;
  width: 100%;
  min-height: 53px;
  border-bottom: 1px solid #C0C0C0; }

.page-header__logo img {
  position: absolute;
  top: 7px;
  left: 44%;
  width: 44px;
  height: 35px;
  z-index: 1; }

.page-header__search {
  position: absolute;
  top: 3px;
  left: 12%;
  z-index: 1; }

.page-header__favorites img {
  position: absolute;
  top: 6px;
  left: 79%;
  z-index: 1; }

.page-header__basket img {
  position: absolute;
  top: 6px;
  left: 89%;
  z-index: 1; }

.page-header__ticker {
  display: none; }

.page-header__btn-menu {
  padding: 5px;
  position: absolute;
  top: 8px;
  left: 12px;
  cursor: pointer;
  transition: right .23s;
  z-index: 3;
  width: 20px;
  transform: translateZ(0);
  backface-visibility: hidden; }

.page-header__btn-menu span {
  display: block;
  height: 0.7pt;
  background-color: #444;
  margin: 5px 0 0;
  transition: all .1s linear .23s;
  position: relative; }

.page-header__ticker:checked ~ .page-header__btn-menu {
  left: 325px;
  top: 2px;
  width: 16px; }

.page-header__ticker:checked ~ .main-nav {
  left: 0; }

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__first {
  transform: rotate(45deg); }

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__second {
  display: none; }

.page-header__ticker:checked ~ .page-header__btn-menu span.page-header__third {
  transform: rotate(-45deg);
  top: -6px; }

.page-header__search-button {
  display: none; }

.page-header__user {
  display: none; }

@media (min-width: 1024px) {
  .page-header {
    border-bottom: none; }
  .page-header__wrapper {
    position: relative;
    max-width: 93.5rem;
    margin: 0 auto; }
  .page-header__btn-menu {
    display: none; }
  .page-header__logo img {
    top: 50px;
    left: 48%;
    width: 64px;
    height: 48px; }
  .page-header__search {
    top: 4px;
    left: 87%; }
  .page-header__search-button {
    display: block;
    width: 160px;
    height: 28px;
    border: 2px solid white;
    background-color: #f2f2f2;
    border-radius: 25px; }
  .page-header__search img {
    position: absolute;
    top: -8px;
    left: 115px; }
  .page-header__favorites img {
    top: 90px;
    left: 93%; }
  .page-header__basket img {
    top: 90px;
    left: 96%; }
  .page-header__user {
    display: block; }
  .page-header__user img {
    position: absolute;
    top: 90px;
    left: 90%;
    z-index: 1; } }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main-nav {
  display: block;
  position: fixed;
  list-style: none;
  padding-top: 23px;
  margin: 0;
  box-sizing: border-box;
  width: 350px;
  background-color: white;
  box-shadow: -8px 20px 16px 10px #444;
  height: 100%;
  top: 0;
  left: -400px;
  transition: left .3s;
  z-index: 2;
  transform: translateZ(0);
  backface-visibility: hidden;
  overflow: scroll; }

.main-nav__list {
  margin: 0;
  padding: 0;
  margin-top: 8px;
  padding-bottom: 5px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  border-bottom: 1px solid #C0C0C0; }

.main-nav__item {
  list-style: none; }

.main-nav__item a {
  text-transform: uppercase;
  text-decoration: none;
  color: #70707070;
  font-size: 14px; }

.main-nav__item--active a {
  color: black;
  padding-bottom: 5px;
  border-bottom: 3px solid black; }

@media (min-width: 1024px) {
  .main-nav {
    position: static;
    padding-top: 100px;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.5);
    box-shadow: none;
    overflow: hidden; }
  .main-nav__list {
    justify-content: flex-start;
    padding-left: 60px;
    padding-bottom: 12px;
    max-width: 90rem;
    margin: 0 auto; }
  .main-nav__item {
    padding-right: 30px; }
  .main-nav__item--active a {
    padding-bottom: 12px; }
  .main-nav__item a:hover {
    padding-bottom: 12px;
    border-bottom: 3px solid black;
    color: black; } }

.main-menu__list {
  margin: 0;
  padding: 15px 0 0 20px;
  border-bottom: 1px solid #C0C0C0; }

.main-menu__item {
  position: relative;
  padding-bottom: 20px;
  list-style: none; }

.main-menu__item a {
  text-decoration: none;
  color: black;
  font-size: 16px; }

.main-menu__item::after {
  content: "";
  position: absolute;
  left: 306px;
  transform: rotate(125deg);
  width: 1px;
  height: 6px;
  background-color: #000; }

.main-menu__item::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 306px;
  transform: rotate(45deg);
  width: 1px;
  height: 6px;
  background-color: #000; }

@media (min-width: 1024px) {
  .main-menu {
    max-width: 93.7rem;
    margin: 0 auto; }
  .main-menu__list {
    display: flex;
    justify-content: space-around; }
  .main-menu__item {
    padding-bottom: 15px; }
  .main-menu__item::after,
  .main-menu__item::before {
    display: none; }
  .main-menu__item a:hover {
    padding-bottom: 15px;
    border-bottom: 3px solid black; } }

.info-menu {
  padding-left: 18px;
